:root {
  --info-width: 500px; }

main#main {
  margin-top: 40px; }
  main#main.np {
    margin-top: 0; }
  main#main .award-list .award-list-title {
    letter-spacing: .5px;
    font: 600 10px/150% var(--font2);
    margin-bottom: 4px;
    color: var(--color7);
    text-transform: uppercase; }
  main#main .award-list ul {
    margin-right: -10px; }
    main#main .award-list ul li {
      margin-right: 20px; }
      main#main .award-list ul li i {
        margin-right: 10px; }
        main#main .award-list ul li i svg {
          width: 24px;
          height: 24px;
          display: block; }
      main#main .award-list ul li > .award-content {
        flex: 1; }
        main#main .award-list ul li > .award-content .text {
          font: 400 12px/150% var(--font2);
          color: var(--color2); }
        main#main .award-list ul li > .award-content .num {
          text-transform: uppercase;
          font: 600 10px/150% var(--font2);
          color: var(--color2);
          letter-spacing: .5px;
          opacity: .9; }
  main#main .award-list.tab-version ul {
    margin-right: 0; }
    main#main .award-list.tab-version ul li {
      padding: 10px;
      background: var(--color3);
      margin: 10px 0; }
      main#main .award-list.tab-version ul li i svg {
        width: 34px;
        height: 34px; }
      main#main .award-list.tab-version ul li > .award-content .text {
        font: 400 16px/150% var(--font2); }
      main#main .award-list.tab-version ul li > .award-content .num {
        font: 600 11px/150% var(--font2); }
  main#main .contests-grid {
    margin-top: 20px;
    width: 100%; }
    main#main .contests-grid-item-card {
      position: relative;
      width: calc(100% / 2 - 10px);
      background: var(--card-bg) no-repeat center/cover; }
      main#main .contests-grid-item-card:after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: var(--color4);
        opacity: .7;
        z-index: 0; }
      main#main .contests-grid-item-card .content-item {
        position: relative;
        z-index: 1;
        padding: 20px;
        height: 100%; }
        main#main .contests-grid-item-card .content-item .title {
          margin-bottom: 40px;
          flex: 1; }
          main#main .contests-grid-item-card .content-item .title .link {
            color: var(--color2);
            font: 400 15px/150% var(--font2); }
            main#main .contests-grid-item-card .content-item .title .link:hover {
              text-decoration: underline; }
          main#main .contests-grid-item-card .content-item .title .status {
            font: 600 11px/150% var(--font2);
            text-transform: uppercase;
            padding: 2px 10px;
            background: rgba(0, 0, 0, 0.5); }
            main#main .contests-grid-item-card .content-item .title .status.status-1 {
              background: var(--color8); }
            main#main .contests-grid-item-card .content-item .title .status.status-2 {
              background: var(--color9); }
      main#main .contests-grid-item-card .awards .a-title {
        font: 500 11px/150% var(--font2);
        margin-bottom: 10px;
        text-transform: uppercase; }
      main#main .contests-grid-item-card .awards ul {
        width: 100%; }
        main#main .contests-grid-item-card .awards ul li {
          width: calc(100% / 2 - 4px);
          margin: 2px; }
          main#main .contests-grid-item-card .awards ul li:nth-child(2n) {
            display: flex;
            justify-content: flex-end; }
          main#main .contests-grid-item-card .awards ul li .content {
            margin-left: 10px;
            font: 400 12px/150% var(--font2); }
    main#main .contests-grid-item {
      width: 100%;
      min-height: var(--contests-height-item);
      margin: 5px 0;
      background: var(--color4); }
      main#main .contests-grid-item .cover {
        object-fit: cover;
        object-position: center;
        width: calc(var(--contests-height-item) * 2);
        height: var(--contests-height-item);
        margin-right: 10px; }
      main#main .contests-grid-item .right {
        padding: 10px;
        height: inherit;
        flex: 1; }
      main#main .contests-grid-item .title {
        font: 300 16px/150% var(--font2);
        color: var(--color2);
        width: 100%; }
        main#main .contests-grid-item .title:hover span {
          color: var(--color5); }
        main#main .contests-grid-item .title .event-status {
          margin-left: 10px;
          padding: 2px 5px;
          display: inline-block;
          letter-spacing: .5px;
          font: 600 10px/150% var(--font2); }
    main#main .contests-grid-v2 .contests-grid-item-card {
      width: calc(100% / 1 - 10px); }
    main#main .contests-grid-v2 .awards ul li {
      width: auto; }
  main#main .contests-slider {
    height: var(--slider-height);
    margin-bottom: 40px;
    position: relative; }
    main#main .contests-slider .slide-prev,
    main#main .contests-slider .slide-next {
      position: absolute;
      top: 34px;
      bottom: 0;
      z-index: 10;
      width: 100px;
      visibility: visible !important;
      opacity: 1; }
      main#main .contests-slider .slide-prev > svg,
      main#main .contests-slider .slide-next > svg {
        width: 32px;
        height: 32px;
        color: var(--color2) !important; }
        main#main .contests-slider .slide-prev > svg:hover,
        main#main .contests-slider .slide-next > svg:hover {
          color: var(--color2) !important; }
      main#main .contests-slider .slide-prev.uk-invisible,
      main#main .contests-slider .slide-next.uk-invisible {
        opacity: 0; }
    main#main .contests-slider .slide-prev {
      left: 0; }
    main#main .contests-slider .slide-next {
      right: 0; }
    main#main .contests-slider .uk-slideshow-items {
      height: inherit;
      min-height: initial !important; }
      main#main .contests-slider .uk-slideshow-items li {
        height: inherit; }
        main#main .contests-slider .uk-slideshow-items li > img {
          height: inherit;
          width: 100%;
          object-position: center;
          object-fit: cover; }
    main#main .contests-slider .uk-slideshow-nav {
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      position: absolute; }
      main#main .contests-slider .uk-slideshow-nav li a {
        border: 0;
        background: var(--color2);
        opacity: .5; }
      main#main .contests-slider .uk-slideshow-nav li.uk-active a {
        opacity: 1; }
  main#main .contests-body .contests-main {
    flex: 1;
    margin-right: 40px; }
  main#main .contests-body .contests-aside {
    width: 320px; }
    main#main .contests-body .contests-aside .contests-grid-item {
      height: 64px;
      min-height: 64px; }
      main#main .contests-body .contests-aside .contests-grid-item .title {
        font: 300 14px/150% var(--font2); }
      main#main .contests-body .contests-aside .contests-grid-item .cover {
        width: 64px;
        height: 64px; }

#contest-item_hero {
  height: calc(100vh - var(--header-height));
  width: 100%;
  position: relative;
  --contest-item-cover-image: url(''); }
  #contest-item_hero .cover {
    background: var(--contest-item-cover-image) no-repeat center center/cover;
    width: inherit;
    height: inherit;
    z-index: 1; }
  #contest-item_hero .info {
    position: absolute;
    top: 40px;
    right: 40px;
    bottom: 40px;
    width: var(--info-width, 200px);
    z-index: 10;
    background: var(--color1); }
    #contest-item_hero .info > .tabs-header > li {
      width: 50%;
      height: 60px; }
      #contest-item_hero .info > .tabs-header > li.uk-active a {
        color: var(--color5); }
      #contest-item_hero .info > .tabs-header > li a {
        width: 100%;
        text-align: center;
        color: var(--color2);
        text-transform: uppercase;
        font: 500 12px var(--font2); }
    #contest-item_hero .info > .tabs-body {
      flex: 1; }
      #contest-item_hero .info > .tabs-body > li {
        padding: 20px 40px;
        font: 400 14px/150% var(--font2); }

#contest-works .grid-list .grid-list-item {
  width: calc(100% / 4 - 4px) !important; }
