:root {
    --info-width: 500px;
}

@alias {
    w: width;
    h: height;
    z: z-index;
    bs: background-size;
    fs: font-size;
    bg: background;
}


main#main {
    margin-top: 40px;

    &.np {
        margin-top: 0;
    }

    .award-list {
        .award-list-title {
            letter-spacing: .5px;
            font: 600 10px/150% var(--font2);
            margin-bottom: 4px;
            color: var(--color7);
            text-transform: uppercase;
        }

        ul {
            margin-right: -10px;

            li {
                margin-right: 20px;


                i {
                    margin-right: 10px;

                    svg {
                        size: 24px;
                        display: block;
                    }
                }

                >.award-content {
                    flex: 1;


                    .text {
                        font: 400 12px/150% var(--font2);
                        color: var(--color2);
                    }

                    .num {
                        text-transform: uppercase;
                        font: 600 10px/150% var(--font2);
                        color: var(--color2);
                        letter-spacing: .5px;
                        opacity: .9;
                    }
                }
            }
        }

        &.tab-version {

            ul {
                margin-right: 0;

                li {
                    padding: 10px;
                    background: var(--color3);
                    margin: 10px 0;

                    i {

                        svg {
                            size: 34px;

                        }
                    }

                    >.award-content {
                        .text {
                            font: 400 16px/150% var(--font2);
                        }

                        .num {
                            font: 600 11px/150% var(--font2);
                        }
                    }
                }
            }
        }
    }

    .contests-grid {
        margin-top: 20px;
        width: 100%;

        &-item-card {
            position: relative;
            width: calc(100% / 2 - 10px);
            background: var(--card-bg) no-repeat center / cover;

            &:after {
                content: '';
                size: 100%;
                position: absolute;
                top: 0;
                left: 0;
                background: var(--color4);
                opacity: .7;
                z-index: 0;
            }

            .content-item {
                position: relative;
                z-index: 1;
                padding: 20px;
                height: 100%;

                .title {
                    margin-bottom: 40px;
                    flex: 1;

                    .link {
                        color: var(--color2);
                        font: 400 15px/150% var(--font2);

                        &:hover {
                            text-decoration: underline;
                        }
                    }

                    .status {
                        font: 600 11px/150% var(--font2);
                        text-transform: uppercase;
                        padding: 2px 10px;
                        background: rgba(#000, .5);

                        &.status-1 {
                            background: var(--color8);
                        }

                        &.status-2 {
                            background: var(--color9);
                        }
                    }
                }
            }

            .awards {
                .a-title {
                    font: 500 11px/150% var(--font2);
                    margin-bottom: 10px;
                    text-transform: uppercase;
                }

                ul {
                    width: 100%;

                    li {
                        width: calc(100% / 2 - 4px);
                        margin: 2px;

                        &:nth-child(2n) {
                            display: flex;
                            justify-content: flex-end;
                        }

                        i {}

                        .content {
                            margin-left: 10px;
                            font: 400 12px/150% var(--font2);

                            .text {}

                            .num {}
                        }
                    }
                }
            }

            &:hover {}
        }

        &-item {
            width: 100%;
            min-height: var(--contests-height-item);
            margin: 5px 0;
            background: var(--color4);

            .cover {
                object-fit: cover;
                object-position: center;
                size: calc(var(--contests-height-item) * 2) var(--contests-height-item);
                margin-right: 10px;
                // margin-bottom: 10px;
            }

            .content {}

            .right {
                padding: 10px;
                height: inherit;
                flex: 1;
            }

            .title {
                font: 300 16px/150% var(--font2);
                color: var(--color2);
                width: 100%;

                &:hover {

                    span {
                        color: var(--color5);
                    }
                }

                .event-status {
                    margin-left: 10px;
                    padding: 2px 5px;
                    display: inline-block;
                    letter-spacing: .5px;
                    font: 600 10px/150% var(--font2);
                }
            }



            .buttons {}
        }

        &-v2 {
            .contests-grid-item-card {
                width: calc(100% / 1 - 10px);
            }

            .awards ul li {
                width: auto;
            }
        }
    }

    .contests-slider {
        height: var(--slider-height);
        margin-bottom: 40px;
        position: relative;

        .slide-prev,
        .slide-next {
            position: absolute;
            top: 34px;
            bottom: 0;
            z-index: 10;
            width: 100px;
            visibility: visible !important;
            opacity: 1;

            >svg {
                size: 32px;
                color: var(--color2) !important;

                &:hover {
                    color: var(--color2) !important;
                    // opacity: 1;
                }
            }

            &.uk-invisible {
                opacity: 0;
            }
        }

        .slide-prev {
            left: 0;
        }

        .slide-next {
            right: 0;
        }

        .uk-slideshow-items {
            height: inherit;
            min-height: initial !important;



            li {
                height: inherit;
                // position: relative;

                >img {
                    height: inherit;
                    width: 100%;
                    object-position: center;
                    object-fit: cover;
                }
            }
        }

        .uk-slideshow-nav {
            left: 50%;
            bottom: 0;
            transform: translateX(-50%);
            position: absolute;

            li {
                a {
                    border: 0;
                    background: var(--color2);
                    opacity: .5;
                }

                &.uk-active {
                    a {
                        opacity: 1;
                    }
                }
            }
        }
    }

    .contests-body {
        .contests-main {
            flex: 1;
            margin-right: 40px;

        }

        .contests-aside {
            width: 320px;

            .contests-grid-item {
                height: 64px;
                min-height: 64px;

                .title {
                    font: 300 14px/150% var(--font2);
                }

                .cover {
                    size: 64px;
                }
            }
        }
    }
}


#contest-item_hero {


    height: calc(100vh - var(--header-height));
    width: 100%;
    position: relative;
    --contest-item-cover-image: url('');

    .cover {
        background: var(--contest-item-cover-image) no-repeat center center / cover;
        size: inherit;
        z-index: 1;
    }

    .info {
        position: absolute;
        top: 40px;
        right: 40px;
        bottom: 40px;
        // width:400px;
        width: var(--info-width, 200px);
        z-index: 10;
        bg: var(--color1);

        >.tabs-header {
            >li {
                width: 50%;
                height: 60px;

                &.uk-active {
                    a {
                        color: var(--color5);
                    }
                }

                a {
                    width: 100%;
                    text-align: center;
                    color: var(--color2);
                    text-transform: uppercase;
                    font: 500 12px var(--font2);
                }
            }
        }

        >.tabs-body {
            flex: 1;

            >li {
                padding: 20px 40px;
                font: 400 14px/150% var(--font2);
            }
        }
    }
}

#contest-works {
    .grid-list {
        .grid-list-item {
            width: calc(100% / 4 - 4px) !important;
        }
    }
}